import {
    maxLength,
    minLength,
    required,
    requiredIf
} from 'vuelidate/lib/validators';

export const validationRules = {
    address: {
        required
    },
    zip: {
        required,
        minLength: minLength(2),
        maxLength: maxLength(100)
    },
    city: {
        required
    },
    country: {
        required
    },
    region: {
        required: requiredIf(function () {
            return this.regionsLength > 0
        })
    }
}
