                        <div class="projects-in-cart__title">
                            <span class="projects-in-cart__order">{{ index + 1 }}.</span>
                            <p>{{ item.title }}</p>
                        </div>
                        <div>
                            <div class="projects-in-cart__price">
                                {{ item.total | money }}
                            </div>
                            <div class="projects-in-cart__modal">
                                <span
                                    class="edit"
                                    @click="editOrder(index)"
                                >
                                    <PencilOutline />
                                    <span class="tool">
                                        Edit
                                    </span>
                                </span>
                                <span
