                                >
                                {{ item.duedate | moment_bidding_text }}
                            </div>
                        </div>
                        <div class="projects-in-cart__price-mobile">
                            {{ item.total | money }}
                        </div>
                    </div>
                    <div class="projects-in-cart__details">
                        <div class="projects-in-cart__pages">
                            <div>
                                <FileOutlineIcon class="quote-sidebar__icon" />
                                {{ item.pages }}
                            </div>
                        </div>
                        <div
