                            v-if="item.preferred_writers.length"
                            class="projects-in-cart__preferred_writers"
                        >
                            <div>
                                <img
                                    src="@/assets/img/svg/preferred_writers.svg"
                                    class="quote-sidebar__icon"
                                >
                                {{ item.preferred_writers.length }}
                            </div>
                        </div>
