        <footer>
            <button
                class="projects-in-cart__button prev"
                @click="addNewOrder()"
            >
                Add another order
                <span class="custom-tooltip">
                    <p>?</p>
                    <span>If you have more than one project, checkout faster by entering all your projects in your cart and then paying only once.</span>
