import {
    email,
    maxLength,
    minLength,
    required
} from 'vuelidate/lib/validators';

export const validationRules = {
    firstname: {
        required
    },
    lastname: {
        required,
        minLength: minLength(2),
        maxLength: maxLength(100)
    },
    email: {
        required,
        email,
        emailExists(value) {
            // ignore this validator, if any of the previous fail
            if (!this.$v.email.required || !this.$v.email.email) { return true; }

            return new Promise((resolve) => {
                const vm = this
                this.checkEmail({ email: value })
                    .then(() => {
                        resolve(true)
                    })
                    .catch(() => {
                        vm.$v.email.$params.emailExists = true
                        resolve(false)
                    })
            })
        }
    },
    password: {
        required,
        minLength: minLength(7),
        maxLength: maxLength(50),
        async checkValue(value) {
            const result = await this.validatePassword(value)
            this.$v.password.$params.passwordStrengthTest = result
            // ignore this validator, if any of the previous fail
            // if (!this.$v.password.required && !this.$v.password.minLength && !this.$v.password.maxLength && result.value) {
            //     return true
            // }
            return result.value
        }
    }
}
