<template>
    <main-layout
        :bg-wrapper="true"
        :classes="'order-form wrapper'"
        :settings-breadcrumbs="settingsBreadcrumbs"
    >
        <template #content>
            <div
                v-if="getOrdersInCart.length"
                class="row checkout-row"
            >
                <!-- left column -->
                <div class="order-form__left-column">
                    <projects-in-cart
                        @eventDeleteOrder="onEventDeleteOrder"
                        @eventEditOrder="onEventEditOrder"
                        @eventAddNewOrder="onEventAddNewOrder"
                    />
                    <personal-details
                        v-show="!isAuth"
                        :current-step="currentStep"
                        @eventNext="onEventNext"
                    />
                    <phone-verification
                        :current-step="currentStep"
                        @eventPrevious="onEventPrevious"
                        @eventNext="onEventNext"
                    />
                    <contact-details
                        :current-step="currentStep"
                        @eventPrevious="onEventPrevious"
                        @eventNext="onEventNext"
                    />
                    <payment-details
                        :current-step="currentStep"
                        @eventPrevious="onEventPrevious"
                        @eventNext="onEventNext"
                    />
                </div>
                <div class="order-form__right-column">
                    <checkout-sidebar
                        :current-step="currentStep"
                        @eventPay="onEventPay"
                    />
                </div>
                <!-- right column - quote -->
            </div>
            <!-- empty cart shows here -->
            <section v-else>
                <div class="empty-cart">
                    <header class="empty-cart__header">
                        <h1 class="empty-cart__title">
                            <Cart />
                            Orders
                        </h1>
                    </header>
                    <div class="empty-cart__body">
                        <h1 class="empty-cart__body-title">
                            Your cart is empty !
                        </h1>
                        <div class="empty-cart__btn">
                            <router-link
                                class="btn-base btn-base_normal btn-base_border"
                                :to="{ name: 'support_form' }"
                            >
                                Contact us
                            </router-link>

                            <router-link
                                class="btn-base btn-base_normal btn-base_colored"
                                :to="{ name: 'order_new' }"
                            >
                                New order
                            </router-link>
                        </div>
                    </div>
                </div>
            </section>
        </template>
    </main-layout>
</template>

<script>
// import TitleComponent from '@/components/common/TitleComponent'

// page components
import MainLayout from '@/layouts/MainLayout';
import ProjectsInCart from '@/components/checkout/ProjectsInCart.vue'
import PersonalDetails from '@/components/checkout/PersonalDetails.vue'
import PhoneVerification from '@/components/checkout/PhoneVerification.vue'
import ContactDetails from '@/components/checkout/ContactDetails.vue'
import PaymentDetails from '@/components/checkout/PaymentDetails.vue'
import CheckoutSidebar from '@/components/checkout/CheckoutSidebar.vue'
import { scroller } from 'vue-scrollto/src/scrollTo'
// icons
// import ChevronRightIcon from 'mdi-vue/ChevronRight'
import Cart from 'mdi-vue/Cart'

// vuex
import { createNamespacedHelpers, mapGetters } from 'vuex'
import {
    CLEAR_FORM,
    EDIT_ORDER
} from '@/store/modules/order/mutation-types'

import {
    REMOVE_STEP,
    UN_VALIDATED_STEP
} from '@/store/modules/checkout/mutation-types'

import {
    // ACTION_CREATE_BATCH,
    ACTION_DELETE_ORDER
} from '@/store/modules/order/action-types'

const {
    mapMutations: mapOrderMutations,
    mapActions: mapOrderActions
} = createNamespacedHelpers('order')

const {
    mapMutations: mapCheckoutMutations
} = createNamespacedHelpers('checkout')

export default {
    name: 'Checkout',
    components: {
        // TitleComponent,
        MainLayout,
        ProjectsInCart,
        PersonalDetails,
        PhoneVerification,
        ContactDetails,
        PaymentDetails,
        CheckoutSidebar,
        // ChevronRightIcon,
        Cart
    },
    data() {
        return {
            currentStep: 1,
            settingsBreadcrumbs: {
                title: 'Checkout',
                steps: [
                    {
                        title: 'Home',
                        url: '/'
                    },
                    {
                        title: 'New Order',
                        url: '/order/new'
                    },
                    {
                        title: 'Checkout',
                        url: '/checkout'
                    }
                ],
                additionalClass: 'bc-horizontal bc-checkout'
            }
        }
    },
    computed: {
        ...mapGetters('order', [
            'getOrdersInCart'
        ]),
        ...mapGetters('client', [
            'isAuth',
            'isPhoneOnRecord',
            'isAddressOnRecord'
        ])
    },
    mounted() {
        this.updateCurrentStep()
    },
    methods: {
        ...mapOrderMutations([
            CLEAR_FORM,
            EDIT_ORDER
        ]),
        ...mapCheckoutMutations([
            REMOVE_STEP,
            UN_VALIDATED_STEP
        ]),
        ...mapOrderActions([
            // ACTION_CREATE_BATCH,
            ACTION_DELETE_ORDER
        ]),
        scrollTo(to, anchor) {
            const scrollToStep = scroller()
            const offset = -document.getElementById('header').getBoundingClientRect().height

            switch (this.currentStep) {
            case 2:
                scrollToStep(to, 500, { offset: anchor })
                break;
            case 3:
                scrollToStep(to, 500, { offset: anchor })
                break;
            case 4:
                scrollToStep(to, 500, { offset: anchor })
                break;
            default:
                scrollToStep(to, 500, { offset })
                break;
            }
        },
        onEventPrevious() {
            if (this.currentStep === 4) {
                setTimeout(() => {
                    this.scrollTo('.checkout-contact-details', -60)
                }, 20)
            } else if (this.currentStep === 3) {
                setTimeout(() => {
                    this.scrollTo('.checkout-phone-verification', -60)
                }, 20)
            }
            this.currentStep -= 1
        },
        onEventNext(value) {
            switch (value) {
            case 1:
                this.currentStep += 1
                this.scrollTo('.checkout-phone-verification', -60)
                break
            case 2:
                this.currentStep += 1
                this.scrollTo('.checkout-phone-verification', 60)
                break
            case 3:
                this.currentStep += 1
                this.scrollTo('.checkout-contact-details', 60)
                break
            case 4:
                this.scrollTo('.payment-details', 60)
                break
            default:
                break
            }
        },
        async onEventDeleteOrder(index) {
            const orderIndex = index + 1
            const modalTitle = 'Delete Order'
            const modalText = `You are about to delete order ${orderIndex}. Are you sure?`
            if (await this.$root.$confirm(modalTitle, modalText, { color: 'red' })) {
                this[ACTION_DELETE_ORDER]({ index })
            }
        },
        async onEventEditOrder(index) {
            const orderIndex = index + 1
            const modalTitle = 'Edit Order'
            const modalText = `You are about to edit order ${orderIndex}. Are you sure?`
            if (await this.$root.$confirm(modalTitle, modalText, { color: 'red' })) {
                this[EDIT_ORDER](index)
                this.$router.push(
                    {
                        name: 'order_edit',
                        params: {
                            index
                        }
                    }
                )
            }
        },
        async onEventAddNewOrder() {
            const modalTitle = 'New Order'
            const modalText = 'You can add another order to the batch and then pay for all orders in one go. Are you sure?'
            if (await this.$root.$confirm(modalTitle, modalText, { color: 'red' })) {
                this[CLEAR_FORM]()
                this.$router.push({
                    name: 'order_new'
                })
            }
        },
        async onEventPay() {
            const modalTitle = 'Pay Order'
            const modalText = 'Are you sure you want to pay order'
            if (await this.$root.$confirm(modalTitle, modalText, { color: 'red' })) {
                // this[ACTION_CREATE_BATCH]()
            }
        },
        updateCurrentStep() {
            // if logged in, validate and hide step
            if (this.isAuth) {
                this[REMOVE_STEP]({
                    step: 1
                })
                this.currentStep = 2
            }

            // if phone is on record
            if (this.isPhoneOnRecord) {
                this[REMOVE_STEP]({
                    step: 2
                })
                this.currentStep = 3
            }

            // if address is on record
            if (this.isAddressOnRecord) {
                this[REMOVE_STEP]({
                    step: 3
                })
                this.currentStep = 4
            }
            this[UN_VALIDATED_STEP]({ step: this.currentStep, value: false })
        }
    }
}
</script>

