<template>
    <section
        id="payment-details"
        class="order-form-step payment"
    >
        <header class="order-form-step__heading">
            <h1 class="order-form-step__heading">
                Payment Details
            </h1>
            <CheckCircle
                v-if="isFormValid"
                class="order-form-step__confirm-icon"
            />
        </header>
        <section
            v-if="currentStep === 4"
            class="payment-details"
        >
            <div class="payment-details__tabs">
                <div
                    class="payment-details__tabs-item"
                    :class="{'payment-details__tabs-item--selected': paymethod === 'cc'}"
                    @click.prevent="paymethod = 'cc'"
                >
                    Credit cards
                </div>
                <div
                    class="payment-details__tabs-item"
                    :class="{'payment-details__tabs-item--selected': paymethod === 'paypal'}"
                    @click.prevent="paymethod = 'paypal'"
                >
                    PayPal
                </div>
            </div>

            <payment-details-credit-card
                v-if="paymethod===`cc`"
                @eventPaymentMetodIsValid="onPaymentMetodIsValid"
            />

            <payment-details-paypal
                v-if="paymethod===`paypal`"
                @eventPaymentMetodIsValid="onPaymentMetodIsValid"
            />

            <div class="row btn">
                <button
                    class="order-form-step__button prev"
                    @click="goToPrevious"
                >
                    previous
                </button>
                <button
                    class="order-form-step__button order-form-step__button-done next"
                    @click="scrollTo"
                >
                    done
                </button>
            </div>
        </section>
    </section>
</template>

<script>
// types
import {
    SET_VALIDATED_STEP
} from '@/store/modules/checkout/mutation-types.js'

// vuex bindings
import { createNamespacedHelpers } from 'vuex'
import { mapCreditCardFields } from '@/store/modules/checkout/index.js'

// icons
import CheckCircle from 'mdi-vue/CheckCircle.vue'

// components
import { scroller } from 'vue-scrollto/src/scrollTo'
import PaymentDetailsCreditCard from './PaymentDetailsCreditCard.vue'
import PaymentDetailsPaypal from './PaymentDetailsPaypal.vue'

const { mapMutations: mapCheckoutMutations } = createNamespacedHelpers('checkout')

export default {
    name: 'PaymentDetails',
    components: {
        PaymentDetailsCreditCard,
        PaymentDetailsPaypal,
        CheckCircle
    },
    props: {
        currentStep: {
            required: true,
            type: Number
        }
    },
    data() {
        return {
            isFormValid: false
        }
    },
    computed: {
        ...mapCreditCardFields([
            'form_data_client.paymethod'
        ]),
        verifiedStoreCredit() {
            return this.total === 0 && this.store_credit > 0
        }
    },
    async mounted() {
        await this.initPayMethod()
    },
    methods: {
        ...mapCheckoutMutations([
            SET_VALIDATED_STEP
        ]),
        initPayMethod() {
            // initialize paymethod, if empty
            if (this.paymethod === '' && !this.verifiedStoreCredit) {
                this.paymethod = 'cc'
            }
        },
        prevalidate() {
            this.validate()
        },
        onPaymentMetodIsValid(value) {
            this.isFormValid = value
            this[SET_VALIDATED_STEP]({
                step: 4,
                name: 'Payment details',
                value
            })
        },
        goToPrevious() {
            this.$emit('eventPrevious', this.currentStep)
        },
        scrollTo() {
            const scrollToStep = scroller()
            scrollToStep('.quote-sidebar', 500, { offset: -60 })
        }
    }
}
</script>

