<template>
    <section class="order-form-step checkout-phone-verification">
        <header class="order-form-step__heading">
            <h1 class="order-form-step__heading">
                Phone Verification
            </h1>
            <CheckCircle
                v-if="isFormValid"
                class="order-form-step__confirm-icon"
            />
        </header>
        <section
            v-if="currentStep === 2"
            id="phone_step"
        >
            <p class="phone-text">
                This is needed to prevent credit card fraud. We will contact the client only in case of emergency
                with regards to the transaction only.
            </p>
            <div class="row phone-switch">
                <form-switch
                    id="chk_phone_bypass"
                    v-model="chkPhoneBypass"
                />
                <span>I am unable to verify my phone</span>
            </div>
            <div
                v-if="!chkPhoneBypass"
                class="row phone"
            >
                <phone-number
                    v-model="phoneVerified"
                    @phoneValidated="onPhoneValidated"
                />
                <!-- <span>error message: {{ validationMessage($v.phoneValidated) }}</span> -->
            </div>
            <div class="row order-form-step__buttons">
                <button
                    v-if="isTherePreviousStep"
                    class="order-form-step__button prev"
                    @click="goToPrevious"
                >
                    previous
                </button>

                <button
                    :class="isFormValid ? 'order-form-step__button next' : 'order-form-step__button next disable'"
                    @click="prevalidate"
                >
                    next
                </button>
            </div>
        </section>
    </section>
</template>

<script>
// components
import PhoneNumber from '@/components/common/form-elements/phone/PhoneNumber'

import Api from '@/helpers/api';

// lodash
import { findIndex } from 'lodash'

// icons
import CheckCircle from 'mdi-vue/CheckCircle.vue'

// validation
import { validationMixin } from 'vuelidate';
import formValidationMixin from '@/mixins/formValidationMixin.js'
import { validationMessage } from 'vuelidate-messages'
import { validationRules } from '@/validation/checkout/phone/RulesPhoneVerification.js'
import { formMessages } from '@/validation/checkout/phone/MessagesPhoneVerification.js'

// vuex bindings
import {
    SET_VALIDATED_STEP
} from '@/store/modules/checkout/mutation-types'
import { mapGetters, createNamespacedHelpers } from 'vuex'

const { mapMutations: mapCheckoutMutations } = createNamespacedHelpers('checkout')

export default {
    name: 'PhoneVerification',
    components: {
        PhoneNumber,
        CheckCircle
    },
    mixins: [
        validationMixin,
        formValidationMixin
    ],
    props: {
        currentStep: {
            required: true,
            type: Number
        }
    },
    validations: {
        ...validationRules
    },
    data() {
        return {
            phone_codes: [],
            chkPhoneBypass: false,
            phoneVerified: false
        }
    },
    computed: {
        ...mapGetters('checkout', [
            'getValidationState'
        ]),
        ...mapGetters('client', [
            'isClientPhoneVerified'
        ]),
        ...mapGetters('phone', [
            'isPhoneVerified'
        ]),
        isFormValid() {
            return !this.$v.$invalid
        },
        isTherePreviousStep() {
            const stepIndex = findIndex(this.getValidationState, (o) => o.step === this.currentStep)
            return stepIndex > 0
        }
    },
    watch: {
        chkPhoneBypass() {
            this.$v.$touch()
        },
        isFormValid(value) {
            this[SET_VALIDATED_STEP]({
                step: 2,
                name: 'Phone verification',
                value
            })
        }
    },
    async mounted() {
        await this.getCountries()
    },
    methods: {
        ...mapCheckoutMutations([
            SET_VALIDATED_STEP
        ]),
        validationMessage: validationMessage(formMessages),
        async getCountries() {
            const vm = this
            const response = await Api.get('/api/data/countries')
            vm.phone_codes = response.data
        },
        prevalidate() {
            this.validate()
        },
        goToPrevious() {
            this.$emit('eventPrevious', this.currentStep)
        },
        onPhoneValidated() {
            this.phoneVerified = true
        },
        submit() {
            this.$emit('eventNext', this.currentStep)
        }
    }
}
</script>

