<template>
    <section class="order-form-step">
        <header class="order-form-step__heading">
            <h1 class="order-form-step__heading">
                Personal Details
            </h1>
            <CheckCircle
                v-if="isFormValid"
                class="order-form-step__confirm-icon"
            />
        </header>
        <section
            v-if="currentStep === 1"
            id="move_personal"
            class="personal-details"
        >
            <div class="row space-between social">
                <div class="order-form-step__column order-form-step__column--half login">
                    <div class="quote-sidebar__login">
                        Are you an existing client? <span @click="redirectToLogin()">Log in</span> to save time
                    </div>
                </div>

                <div class="order-form-step__column order-form-step__column--half social">
                    <div class="social-or">
                        <span>or</span>
                    </div>
                    <SocialLogin />
                </div>
            </div>
            <div class="row space-between">
                <!-- firstname -->
                <div class="order-form-step__column order-form-step__column--half">
                    <form-input
                        v-model="$v.firstname.$model"
                        legend="First name"
                        :field.sync="firstname"
                        name="firstname"
                        :is-valid="$v.firstname.$dirty && !$v.firstname.$anyError"
                        :errors="validationMessage($v.firstname)"
                        @input.native="$v.firstname.$touch()"
                        @blur.native="$v.firstname.$touch()"
                    />
                </div>
                <!-- firstname -->
                <!-- lastname -->
                <div class="order-form-step__column order-form-step__column--half">
                    <form-input
                        v-model="$v.lastname.$model"
                        legend="Last name"
                        :field.sync="lastname"
                        name="lastname"
                        :is-valid="$v.lastname.$dirty && !$v.lastname.$anyError"
                        :errors="validationMessage($v.lastname)"
                        @input.native="$v.lastname.$touch()"
                        @blur.native="$v.lastname.$touch()"
                    />
                </div>
                <!-- lastname -->
            </div>
            <div class="row space-between">
                <!-- email -->
                <div class="order-form-step__column order-form-step__column--half">
                    <form-input
                        v-model="emailValue"
                        legend="Email"
                        :field.sync="emailValue"
                        name="email"
                        :is-valid="$v.email.$dirty && !$v.email.$anyError"
                        :errors="validationMessage($v.email)"
                        @input.native="$v.email.$touch()"
                        @blur.native="$v.email.$touch()"
                    />
                </div>
                <!-- email -->
                <!-- password -->
                <div class="order-form-step__column order-form-step__column--half">
                    <form-input
                        v-model="$v.password.$model"
                        legend="Password"
                        :field.sync="password"
                        name="password"
                        :is-valid="$v.password.$dirty && !$v.password.$anyError"
                        :errors="validationMessage($v.password)"
                        :append-icon="showPassword ? 'EyeIcon' : 'EyeOffIcon'"
                        :type="showPassword ? 'text' : 'password'"
                        @clicked="showPassword = !showPassword"
                        @input.native="$v.password.$touch()"
                        @blur.native="$v.password.$touch()"
                    />
                    <progress-linear :validate="this.$v.password.$params.passwordStrengthTest" />
                </div>
                <!-- password -->
            </div>
            <div class="row">
                <custom-button
                    :class="isFormValid ? 'order-form-step__button next' : 'order-form-step__button next disable'"
                    :loading="loading"
                    @click.native="prevalidate()"
                >
                    Next
                </custom-button>
            </div>
        </section>
    </section>
</template>

<script>
import { debounce } from 'lodash'

// event bus
import { eventBus } from '@/helpers/event-bus/'

// icons
import CheckCircle from 'mdi-vue/CheckCircle.vue'

// components
import SocialLogin from '@/components/common/SocialLogin'
import ProgressLinear from '@/components/common/ProgressLinear'

// validation
import { validationMixin } from 'vuelidate';
import formValidationMixin from '@/mixins/formValidationMixin.js'
import passwordValidationMixin from '@/mixins/passwordValidationMixin'
import { validationMessage } from 'vuelidate-messages'
import { validationRules } from '@/validation/checkout/RulesPersonalDetails.js'
import { formMessages } from '@/validation/checkout/MessagesPersonalDetails.js'

// types
import {
    ACTION_CLIENT_CHECK_EMAIL,
    ACTION_CREATE_CLIENT_SIMPLE,
    ACTION_LOGIN_CLIENT
} from '@/store/modules/client/action-types'

import {
    SET_VALIDATED_STEP,
    REMOVE_STEP
} from '@/store/modules/checkout/mutation-types'

// vuex bindings
import { createNamespacedHelpers } from 'vuex'
import { mapClientFormFields } from '@/store/modules/checkout/'

const { mapMutations: mapCheckoutMutations } = createNamespacedHelpers('checkout')
const { mapActions: mapClientActions } = createNamespacedHelpers('client')

export default {
    name: 'PersonalDetails',
    components: {
        SocialLogin,
        CheckCircle,
        ProgressLinear
    },
    mixins: [
        validationMixin,
        formValidationMixin,
        passwordValidationMixin
    ],
    validations: {
        ...validationRules
    },
    props: {
        currentStep: {
            required: true,
            type: Number
        }
    },
    data() {
        return {
            loading: false,
            showPassword: false,
            emailValue: ''
        }
    },
    computed: {
        ...mapClientFormFields([
            'form_data_client.firstname',
            'form_data_client.lastname',
            'form_data_client.email',
            'form_data_client.password'
        ]),
        isFormValid() {
            return !this.$v.$invalid
        }
    },
    watch: {
        isFormValid(value) {
            this[SET_VALIDATED_STEP]({
                step: 1,
                name: 'Personal details',
                value
            })
        },
        emailValue: debounce(function (newEmail) {
            this.email = newEmail
        }, 300)
    },
    mounted() {
        this.emailValue = this.email
    },
    methods: {
        ...mapCheckoutMutations([
            REMOVE_STEP,
            SET_VALIDATED_STEP
        ]),
        ...mapClientActions([
            ACTION_CREATE_CLIENT_SIMPLE,
            ACTION_CLIENT_CHECK_EMAIL,
            ACTION_LOGIN_CLIENT
        ]),
        async checkEmail(value) {
            await this[ACTION_CLIENT_CHECK_EMAIL](value)
        },
        validationMessage: validationMessage(formMessages),
        redirectToLogin() {
            // save current url to state for future redirect
            this.$router.push({
                name: 'login',
                query: {
                    redirect: window.location.href
                }
            })
        },
        prevalidate() {
            this.validate()
        },
        async registerClient() {
            const vm = this
            try {
                vm.loading = true
                await this[ACTION_CREATE_CLIENT_SIMPLE]()
                eventBus.$emit('showSnackBar', 'Your account has been created', 'success')
            } catch (err) {
                eventBus.$emit('showSnackBar', 'Failed to register', 'error')
            } finally {
                vm.loading = false
            }
        },
        async loginClient() {
            const vm = this
            try {
                // login client
                vm.loading = true
                await this[ACTION_LOGIN_CLIENT]({
                    email: vm.email,
                    password: vm.password,
                    remember_me: false
                })
                this[REMOVE_STEP]({
                    step: 1
                })
                eventBus.$emit('showSnackBar', 'You are logged in', 'success')
            } catch (err) {
                console.log(err)
                eventBus.$emit('showSnackBar', 'Cannot login user', 'error')
            } finally {
                vm.loading = false
            }
        },
        async submit() {
            try {
                await this.registerClient()
                await this.loginClient()
                this.$emit('eventNext', this.currentStep)
            } catch (error) {
                // console.log(error)
                eventBus.$emit('showSnackBar', error.response.data.message, 'error')
            }
        }
    }
}
</script>

