<template>
    <div class="quote-sidebar">
        <div class="gradient-border" />

        <header class="quote-sidebar__header">
            <div class="row space-between">
                <h2>Checkout here</h2>
            </div>
        </header>
        <section>
            <div class="quote-sidebar__data">
                <span class="quote-sidebar__data-name">Total price: </span>
                <span class="quote-sidebar__data-value">
                    <animated-number
                        class="quote-sidebar__total-amount"
                        :value="getCartTotal"
                        :duration="300"
                        :format-value="formatToPrice"
                    />
                </span>
            </div>
        </section>
        <footer>
            <div class="quote-sidebar__row">
                <form-checkbox
                    v-model="$v.chk_agreement.$model"
                    :field.sync="chk_agreement"
                    class="order-form-step__checkbox"
                    :errors="agreementErrors"
                />
                <span
                    class="term-service"
                    :class="shake && 'term-service--shake'"
                >
                    I agree with the <span @click="openAgreementModal()">term of service</span>
                </span>
                <p class="agreement-errors">
                    {{ agreementErrors }}
                </p>
            </div>
            <div class="quote-sidebar__row">
                <button
                    :class="isSubmitAllowed ? `quote-sidebar__btn active` : `quote-sidebar__btn disable`"
                    :disabled="!isSubmitAllowed"
                    @click.prevent="submit"
                >
                    PAY
                </button>
            </div>
        </footer>
        <agreement-modal ref="agreement-modal" />
    </div>
</template>

<script>
// validation
import { validationMixin } from 'vuelidate';

import AnimatedNumber from 'animated-number-vue'
import AgreementModal from '@/components/modals/AgreementModal'

import { mapGetters } from 'vuex'

export default {
    name: 'CheckoutSidebar',
    components: {
        AnimatedNumber,
        AgreementModal
    },
    mixins: [
        validationMixin
    ],
    props: {
        currentStep: {
            required: true,
            type: Number,
            default: 1
        }
    },
    validations: {
        chk_agreement: {
            checkIfAgreed() {
                return this.chk_agreement
            }
        }
    },
    data() {
        return {
            chk_agreement: false,
            shake: false
        }
    },
    computed: {
        ...mapGetters('checkout', [
            'getCheckoutFormStatus'
        ]),
        ...mapGetters('order', [
            'getCartTotal'
        ]),
        agreementErrors() {
            const errors = []
            if (!this.$v.chk_agreement.$dirty) {
                return errors.join(' ')
            }
            !this.$v.chk_agreement.checkIfAgreed && errors.push('You must read and accept the terms of service')
            return errors.join(' ')
        },
        isSubmitAllowed() {
            // allow submit only on last step (3)
            // if (this.value !== 3) return false
            return this.getCheckoutFormStatus
        }
    },
    watch: {
        agreementErrors() {
            if (this.agreementErrors.length) {
                this.shake = true
                setTimeout(() => {
                    this.shake = false
                }, 500);
            }
        }
    },
    methods: {
        formatToPrice(value) {
            return `$ ${value.toFixed(2).replace(/\B(?=(\d{3})+(?!\d))/g, ',')}`;
        },
        openAgreementModal() {
            this.$refs['agreement-modal'].open()
        },
        submit() {
            this.$v.$touch()
            if (!this.$v.$invalid) {
                this.$emit('eventPay')
            }
        }
    }
}
</script>

<style lang="scss">
    .quote-sidebar {
        position: sticky;
        top: 100px;
        &__data {
            align-items: center;
            padding: 0 20px 10px;
        }
        &__row {
            .term-service {
                span {
                    color: #ff690c;
                    cursor: pointer;
                }
            }
        }
    }

    @media (max-width: 1200px) {
        .quote-sidebar {
            top: 80px;
        }
    }
    @keyframes shakeX {
        from, to {
            transform: translate3d(0, 0, 0);
        }

        10%, 30%, 50%, 70%, 90% {
            transform: translate3d(-5px, 0, 0);
        }

        20%, 40%, 60%, 80% {
            transform: translate3d(5px, 0, 0);
        }
    }
    .term-service--shake {
        animation: .2s shakeX;
    }
</style>
