<template>
    <section class="order-form-step checkout-contact-details">
        <header class="order-form-step__heading">
            <h1 class="order-form-step__heading">
                Contact Details
            </h1>
            <CheckCircle
                v-if="isFormValid"
                class="order-form-step__confirm-icon"
            />
        </header>
        <section
            v-if="currentStep === 3"
            class="contact-details"
        >
            <div class="row space-between">
                <div class="order-form-step__column order-form-step__column--half">
                    <form-input
                        v-model="$v.address.$model"
                        legend="Address"
                        :field.sync="address"
                        name="address"
                        :errors="validationMessage($v.address)"
                        @input.native="$v.address.$touch()"
                        @blur.native="$v.address.$touch()"
                    />
                </div>

                <div class="order-form-step__column order-form-step__column--half">
                    <form-input
                        v-model="$v.zip.$model"
                        legend="ZIP"
                        :field.sync="zip"
                        name="zip"
                        :errors="validationMessage($v.zip)"
                        type="text"
                        inputmode="numeric"
                        pattern="[0-9]*"
                        @input.native="$v.zip.$touch()"
                        @blur.native="$v.zip.$touch()"
                    />
                </div>
            </div>
            <div class="row space-between">
                <div class="order-form-step__column order-form-step__column--half">
                    <form-select-countries
                        v-model="$v.country.$model"
                        :country="country"
                        label="Country"
                        tabindex="0"
                        :field.sync="country"
                        :errors="validationMessage($v.country)"
                        :is-valid="!$v.country.$anyError && !$v.country.$invalid"
                        @input.native="$v.country.$touch()"
                        @blur.native="$v.country.$touch()"
                    />
                </div>

                <div class="order-form-step__column order-form-step__column--half">
                    <form-input
                        v-model="$v.city.$model"
                        legend="City"
                        :field.sync="city"
                        :errors="validationMessage($v.city)"
                        :is-valid="$v.city.$dirty && !$v.city.$anyError"
                        @input.native="$v.city.$touch()"
                        @blur.native="$v.city.$touch()"
                    />
                </div>
            </div>
            <div
                v-if="regionsLength > 0"
                class="order-form-step__column--half order-form-step__column-region"
            >
                <form-select-region
                    v-model="$v.region.$model"
                    :region="region"
                    :country="country"
                    label="Region"
                    tabindex="0"
                    :field.sync="region"
                    :errors="validationMessage($v.region)"
                    :is-valid="!$v.region.$anyError && !$v.region.$invalid"
                    @input.native="$v.region.$touch()"
                    @blur.native="$v.region.$touch()"
                    @checkRequired="regionsLength = $event"
                />
            </div>
            <div
                style="display: none"
                class="order-form-step__column--half order-form-step__column-region"
            >
                <form-select-region
                    v-model="$v.region.$model"
                    :region="region"
                    :country="country"
                    label="Region"
                    tabindex="0"
                    :field.sync="region"
                    :errors="validationMessage($v.region)"
                    :is-valid="!$v.region.$anyError && !$v.region.$invalid"
                    @input.native="$v.region.$touch()"
                    @blur.native="$v.region.$touch()"
                    @checkRequired="regionsLength = $event"
                />
            </div>

            <div class="row order-form-step__buttons">
                <button
                    class="order-form-step__button prev"
                    @click="goToPrevious"
                >
                    previous
                </button>

                <button
                    :class="isFormValid ? 'order-form-step__button next' : 'order-form-step__button next disable'"
                    @click="prevalidate"
                >
                    Next
                </button>
            </div>
        </section>
    </section>
</template>

<script>
// icons
import CheckCircle from 'mdi-vue/CheckCircle.vue'

// validation
import { validationMixin } from 'vuelidate';
import formValidationMixin from '@/mixins/formValidationMixin.js'
import { validationMessage } from 'vuelidate-messages'
import { validationRules } from '@/validation/checkout/RulesContactDetails.js'
import { formMessages } from '@/validation/checkout/MessagesContactDetails.js'

// types
import {
    SET_VALIDATED_STEP,
    UPDATE_FIELDS
} from '@/store/modules/checkout/mutation-types'

// vuex bindings
import { createNamespacedHelpers, mapGetters } from 'vuex'
import { mapClientFormFields } from '@/store/modules/checkout/'

const { mapMutations: mapCheckoutMutations } = createNamespacedHelpers('checkout')

export default {
    name: 'ContactDetails',
    components: {
        CheckCircle
    },
    mixins: [
        validationMixin,
        formValidationMixin
    ],
    validations: {
        ...validationRules
    },
    props: {
        currentStep: {
            required: true,
            type: Number
        }
    },
    data() {
        return {
            regionsLength: 0
        }
    },
    computed: {
        ...mapClientFormFields([
            'form_data_client.address',
            'form_data_client.city',
            'form_data_client.zip',
            'form_data_client.country',
            'form_data_client.region'
        ]),
        ...mapGetters('client', [
            'getDataFields',
            'isAddressOnRecord'
        ]),
        isFormValid() {
            return !this.$v.$invalid
        }
    },
    watch: {
        isFormValid(value) {
            console.log(value, 'isFormValid');
            this[SET_VALIDATED_STEP]({
                step: 3,
                name: 'Contact details',
                value
            })
        },
        regionsLength(value) {
            this.region = ''
            this.$emit('checkRequired', value)
        }
    },
    mounted() {
        if (this.isAddressOnRecord
            && !(this.address.length && this.city.length && this.zip.length && this.country.length)) {
            this[UPDATE_FIELDS](this.getDataFields)
        }
    },
    methods: {
        ...mapCheckoutMutations([
            SET_VALIDATED_STEP,
            UPDATE_FIELDS
        ]),
        validationMessage: validationMessage(formMessages),
        prevalidate() {
            this.validate()
        },
        goToPrevious() {
            this.$emit('eventPrevious', this.currentStep)
        },
        submit() {
            this.$emit('eventNext', this.currentStep)
        }
    }
}
</script>

<style lang="scss">
    .contact-details {
        .order-form-step__column--half:last-child input {
            padding-left: 10px;
        }
        .row {
            align-items: center;

            &:first-child {
                .order-form-step__column {
                    margin: 20px 0 0;
                }
            }

            .form-select__fieldset::after {
                right: 10px;
            }

            &.phone {
                margin: 20px 0 10px;

                .form-switch {
                    margin-right: 20px;
                }
            }

            &.billing-adress {
                margin-bottom: 20px;
            }
        }
        .order-form-step__column-region {
            margin-bottom: 20px;

            input {
                padding-left: 10px;
            }
        }
    }

    @media (max-width: 1200px) {
        .contact-details {
            .row.space-between:first-child {
                .order-form-step__column {
                    margin-bottom: 5px;
                }
            }
        }
    }

    @media (max-width: 576px) {
        .contact-details {
            margin-top: -5px;

            .row.space-between {
                flex-direction: column;

                &:nth-child(2) {
                    .order-form-step__column:last-child {
                        margin-bottom: 20px;
                    }
                }

                .order-form-step__column {
                    width: 100%;
                    margin-bottom: 10px;
                }
            }
            .billing-adress {
                margin: 10px 0;
            }

            .order-form-step__column-region {
                width: 100%;
            }
        }
    }
</style>
